import React, { Component } from 'react';
import { Button, Input, Table, CardHeader, Popover, PopoverBody, PopoverHeader } from 'reactstrap';
import { loadAccountTransactions, loadAutoTransactions } from '../../eshop/eshop.js'
import classnames from 'classnames';
import { FormGroup, InputGroupText, InputGroup, InputGroupAddon, Label, PaginationItem, PaginationLink, Pagination } from 'reactstrap'
import { Modal, ModalBody, ModalFooter, ModalHeader, Alert, DropdownToggle, Dropdown, DropdownItem, DropdownMenu } from 'reactstrap'
import { getCurrentHost, calcAccountId, createPeerId, saveUserSec, simpleHash, loadLocalAccount, getShareHost } from '../../ggv/js/gegeUtil'
import BtbSecret from '../BtbSecret/BtbSecret'
import AccountDialog from '../AccountDialog/AccountDialog'
import PopHint from '../PopHint/PopHint'
import DepositDialog from '../DepositDialog/DepositDialog'
const { loadProductList, doBShopTransaction, getIssueReason, loadProductDetails, loadIssueTransactions, shareDecrypt, shareEncrypt, loadEncryptedInfo } = require('../../eshop/eshop')
const moment = require('moment')
let host = getShareHost();
const get = require('simple-get')

class ActionButton extends Component {
  constructor(props) {
    super(props);
    this.state = {
      product: this.props.product, productDetail: this.props.productDetail,
      winner: this.props.winner, paying: false, confirm: false, day7refund: false,
      issue: false, drop: false, selectIssueAction: false, selectActionTitle: '选择纠纷操作类型',
      issueReason: 0, issues: [], issuePage: 1, renderDesposit: false
    };
    this.fee = '1'
    this.deadline = 5
  }

  componentDidMount() {
    let account = loadLocalAccount();
    if (account && account.secret) {
      this.secret = account.secret;
      this.account = account;
    }
  }

  onSecret(sec) {
    if (sec) {
      this.secret = sec
      let rsId = calcAccountId(this.userSec);
      this.setState({ rsId: rsId })
    }
  }

  togglePay10p() {
    this.setState({
      paying: !this.state.paying, dialogMsg: ''
    });
  }

  pay10P() {
    //this.toggleDetail()
    this.togglePay10p()
  }

  toggleDrop() {
    this.setState({
      drop: !this.state.drop, dialogMsg: ''
    });
  }

  toggleDecrypt() {

    if (!this.state.decrypt && this.account) {
      loadEncryptedInfo(this.state.product, this.account.rsId, ((ok, infos) => {
        if (ok && infos) {
          this.setState({ infoBlocks: infos })
        }
      }).bind(this));
    } else {
      this.setState({ decrypted: null })
    }
    //
    let dialogMsg = '';
    if (!this.account) {
      dialogMsg = '未登录'
    }
    this.setState({
      decrypt: !this.state.decrypt, dialogMsg: dialogMsg
    });
  }

  toggleSending() {
    this.setState({
      sending: !this.state.sending, dialogMsg: ''
    });
  }

  toggleIssue() {
    this.setState({
      issue: !this.state.issue, dialogMsg: ''
    });
  }

  toggleHelp() {
    this.setState({
      voteHelp: !this.state.voteHelp, dialogMsg: ''
    });
  }

  toggleVotes() {
    if (!this.state.votes) {
      loadIssueTransactions(this.state.product, this.state.issuePage, 6, (ok, issues) => {
        if (ok && issues)
          this.setState({ issues })
      })
    }
    this.setState({
      votes: !this.state.votes, dialogMsg: '', issuePage: 1
    });

  }

  toggleRejectConfirm() {
    this.setState({
      rejectConfirm: !this.state.rejectConfirm, dialogMsg: ''
    });
  }

  toggle7dayRefund() {
    this.setState({
      day7refund: !this.state.day7refund, dialogMsg: ''
    });
  }

  renderActionButtons() {
    let debug = false
    if (debug) {
      return (<div>
        <Button color="danger" onClick={this.debug.bind(this)}>debug</Button>
        <Button color="danger" onClick={this.pay10P.bind(this)}>收货信息</Button>
        <Button color="danger" onClick={this.toggleDrop.bind(this)}>买家放弃</Button>
        <Button color="danger" onClick={this.toggleSending.bind(this)}>卖家发货</Button>
        <Button color="danger" onClick={this.toggle7dayRefund.bind(this)}>8天无理由退货</Button>
        <Button color="danger" onClick={this.toggleIssue.bind(this)}>买家申请纠纷处理</Button>
        <Button color="danger" onClick={this.toggleRejectConfirm.bind(this)}>卖家确认退货</Button>
        <Button color="danger" onClick={this.toggleIssue.bind(this)}>卖家申请纠纷处理</Button>
        <Button color="danger" onClick={this.toggleIssue.bind(this)}>纠纷投票</Button>
        <Button color="danger" onClick={this.toggleVotes.bind(this)}>投票 列表</Button>
        <Button color="danger" onClick={this.toggleConfirm.bind(this)}>买家确认收货</Button>
        <Button color="danger" onClick={this.toggleRejectConfirm.bind(this)}>卖家确认退货</Button>
        <Button color="danger" onClick={this.toggleDecrypt.bind(this)}>解密买家地址</Button>
      </div>)
    }
    let status = this.state.product.status;
    if (status == 3)
      return (<div>
        <Button color="danger" onClick={this.pay10P.bind(this)}>发送收货信息</Button>
        <Button color="danger" onClick={this.toggleDrop.bind(this)}>买家放弃</Button>
      </div>)
    if (status == 4)
      return (<div>
        <Button color="danger" onClick={this.toggleDecrypt.bind(this)}>解密买家地址</Button>
        <Button color="danger" onClick={this.toggleSending.bind(this)}>卖家发货</Button>
      </div>)
    if (status == 5)
      return (<div>
        <Button color="danger" onClick={this.toggleDecrypt.bind(this)}>解密发货信息</Button>
        <Button color="danger" onClick={this.toggleConfirm.bind(this)}>买家确认收货</Button>
        <Button color="danger" onClick={this.toggle7dayRefund.bind(this)}>8天无理由退货</Button>
        <Button color="danger" onClick={this.toggleIssue.bind(this)}>买家申请纠纷处理</Button>
      </div>)
    if (status == 6)
      return (<div>
        <Button color="danger" onClick={this.toggleDecrypt.bind(this)}>解密发货信息</Button>
        <Button color="danger" onClick={this.toggleRejectConfirm.bind(this)}>卖家确认退货</Button>
        <Button color="danger" onClick={this.toggleIssue.bind(this)}>卖家申请纠纷处理</Button>
      </div>)
    if (status == 10)
      return (<div>
        <Button color="danger" onClick={this.toggleVotes.bind(this)}>查看投票列表</Button>
        <Button color="danger" onClick={this.toggleHelp.bind(this)}>邀请好友帮忙投票</Button>
        <Button color="danger" onClick={this.toggleIssue.bind(this)}>纠纷投票</Button>
        <Button color="danger" onClick={this.toggleConfirm.bind(this)}>买家确认收货</Button>
        <Button color="danger" onClick={this.toggleRejectConfirm.bind(this)}>卖家确认退货</Button>
      </div>)
    return null;
  }

  updateFee(e) {
    if (e && e.target) {
      this.fee = e.target.value;
      if (this.fee.length == 0) {
        this.fee = '1';
      }
    }
  }

  updateSellerRefund(e) {
    if (e && e.target) {
      this.sellerRefund = e.target.value;
    }
  }

  updateIssueNote(e) {
    if (e && e.target) {
      this.issueNote = e.target.value;
      if (this.issueNote.length > 100) {
        this.issueNote = this.issueNote.substring(0, 99);
      }
    }
  }

  updateBuyerRefund(e) {
    if (e && e.target) {
      this.buyerRefund = e.target.value;
    }
  }

  updateDeadline(e) {
    if (e && e.target) {
      this.deadline = e.target.value;
      if (this.deadline.length == 0) {
        this.deadline = 5;
      }
    }
  }


  updateAddress(e) {
    if (e && e.target) {
      this.address = e.target.value;
      this.updateAddressBlock()
    }
  }

  updateProductAddress(e) {
    if (e && e.target) {
      this.address = e.target.value;
    }
  }
  updateContact(e) {
    if (e && e.target) {
      this.contact = e.target.value;
      this.updateAddressBlock()
    }
  }
  updatePhone(e) {
    if (e && e.target) {
      this.phone = e.target.value;
      this.updateAddressBlock()
    }
  }

  updateAddressBlock() {
    let clear = '联系人:' + this.contact + ' 地址:' + this.address + ' 联系方式:' + this.phone;
    clear = JSON.stringify(clear);
    let pk = this.state.productDetail.seller;
    let addressHash = simpleHash(clear);
    let addressBlock = shareEncrypt(this.secret, pk, this.state.product.transactionId, clear);
    this.setState({ addressBlock: addressBlock, addressHash: addressHash });
  }

  updateTracking(e) {
    if (e && e.target) {
      let tracking = e.target.value;
      //get winner pk
      let trackingHash = simpleHash(tracking);
      let pk = this.state.productDetail.winner.pk;
      let trackingBlock = shareEncrypt(this.secret, pk, this.state.product.transactionId, tracking);
      this.setState({ trackingBlock: trackingBlock, trackingHash: trackingHash });
    }
  }

  debug() {
    let pk = ''
    let nonce = ''
    let block = ''
    let clear = shareDecrypt(this.secret, pk, nonce, block);
    console.log(clear)
  }

  renderdialogMsg() {
    if (this.state.renderDesposit) {
      return (<div>
        <Label>{this.state.dialogMsg}</Label>
        <DepositDialog />
      </div>)
    } else
      if (this.state.dialogMsg) {
        return (<Label>{this.state.dialogMsg}</Label>)
      }
    return null;
  }


  doPaying() {
    let transactionId = this.state.product.transactionId;
    let peer = createPeerId(this.secret);
    let hash = this.state.addressHash;
    let addressBlock = this.state.addressBlock;
    let data = {
      action: 'transaction', type: 'sAccept', amount: '0', hash: hash,
      fee: this.fee, dealine: this.deadline, sid: transactionId, pk: peer.pk, accountId: peer.aid,
      addressBlock: addressBlock
    };
    doBShopTransaction(this.secret, data, ((ok, msg) => {
      if (ok && msg && msg.ok) {
        saveUserSec(this.secret)
        this.setState({ dialogMsg: '成功,等待机器人验证(大约一分钟)', success: true })
      } else {
        if (msg && !msg.ok && '1001' === msg.code) {
          this.setState({ dialogMsg: '账户余额不足。请充值或去朋友圈领取现金红包。', renderDesposit: true })
        } else
          this.setState({ dialogMsg: '失败' })
      }
    }).bind(this))
  }

  do7dayrefund() {
    let transactionId = this.state.product.transactionId;
    let peer = createPeerId(this.secret);
    //check peer is winner
    let info = this.state.trackingBlock;
    let hash = simpleHash(info)
    info = new Buffer(info).toString('base64')
    let data = {
      action: 'transaction', type: 'sAction', hash, info: info, accountId: peer.aid,
      fee: this.fee, dealine: this.deadline, sid: transactionId, saction: '20'
    };
    doBShopTransaction(this.secret, data, ((ok, msg) => {
      if (ok && msg && msg.ok) {
        saveUserSec(this.secret)
        this.setState({ dialogMsg: '成功,等待机器人验证(大约一分钟)', success: true })
      } else {
        if (msg && !msg.ok && '1001' === msg.code) {
          this.setState({ dialogMsg: '账户余额不足。请充值或去朋友圈领取现金红包。', renderDesposit: true })
        } else
          this.setState({ dialogMsg: '失败' })
      }
    }).bind(this))
  }


  doDrop() {
    let transactionId = this.state.product.transactionId;
    let peer = createPeerId(this.secret);
    //check peer is winner
    let hash = '0';
    let data = {
      action: 'transaction', type: 'sAction', hash: hash, accountId: peer.aid,
      fee: this.fee, dealine: this.deadline, sid: transactionId, saction: '4'
    };
    doBShopTransaction(this.secret, data, ((ok, msg) => {
      if (ok && msg && msg.ok) {
        saveUserSec(this.secret)
        this.setState({ dialogMsg: '成功,等待机器人验证(大约一分钟)', success: true })
      } else {
        if (msg && !msg.ok && '1001' === msg.code) {
          this.setState({ dialogMsg: '账户余额不足。请充值或去朋友圈领取现金红包。', renderDesposit: true })
        } else
          this.setState({ dialogMsg: '失败' })
      }
    }).bind(this))
  }

  doIssue() {
    if (this.state.issueReason == 0) {
      this.setState({ dialogMsg: 'Choose(请选择操作类型)' })
      return;
    }
    let transactionId = this.state.product.transactionId;
    let peer = createPeerId(this.secret);
    //check peer is winner
    // 1 not receive,2 not sent back.3 vote
    let reason = this.state.issueReason;
    let wp = this.buyerRefund;
    let sp = this.sellerRefund;
    let info = this.issueNote;
    let hash = 0;
    if (info && info.length > 0) {
      hash = simpleHash(info);
      info = new Buffer(info).toString('base64')
    }
    let data = {
      action: 'transaction', type: 'issue', hash: hash, info: info, pk: peer.pk,
      fee: this.fee, dealine: this.deadline, sid: transactionId, reason: reason,
      wp: wp, sp: sp
    };
    doBShopTransaction(this.secret, data, ((ok, msg) => {
      if (ok && msg && msg.ok) {
        saveUserSec(this.secret)
        this.setState({ dialogMsg: '成功,等待机器人验证(大约一分钟)', success: true })
      } else {
        if (msg && !msg.ok && '1001' === msg.code) {
          this.setState({ dialogMsg: '账户余额不足。请充值或去朋友圈领取现金红包。', renderDesposit: true })
        } else
          this.setState({ dialogMsg: '失败' })
      }
    }).bind(this))
  }

  doRejectConfirm() {
    let transactionId = this.state.product.transactionId;
    let peer = createPeerId(this.secret);
    //check peer is winner
    let data = {
      action: 'transaction', type: 'sAction', accountId: peer.aid, pk: peer.pk,
      fee: this.fee, dealine: this.deadline, sid: transactionId, saction: '21'
    };
    doBShopTransaction(this.secret, data, ((ok, msg) => {
      if (ok && msg && msg.ok) {
        saveUserSec(this.secret)
        this.setState({ dialogMsg: '成功,等待机器人验证(大约一分钟)', success: true })
      } else {
        if (msg && !msg.ok && '1001' === msg.code) {
          this.setState({ dialogMsg: '账户余额不足。请充值或去朋友圈领取现金红包。', renderDesposit: true })
        } else {
          if (msg && !msg.ok && '1001' === msg.code) {
            this.setState({ dialogMsg: '账户余额不足。请充值或去朋友圈领取现金红包。', renderDesposit: true })
          } else
            this.setState({ dialogMsg: '失败' })
        }
      }
    }).bind(this))
  }

  doSending() {
    let transactionId = this.state.product.transactionId;
    let peer = createPeerId(this.secret);
    //check peer is winner
    let info = this.state.trackingBlock;
    let hash = this.state.trackingHash;
    info = new Buffer(info).toString('base64')
    let data = {
      action: 'transaction', type: 'sAction', accountId: peer.aid, pk: peer.pk,
      fee: this.fee, dealine: this.deadline, sid: transactionId, saction: '2', info: info, hash
    };
    doBShopTransaction(this.secret, data, ((ok, msg) => {
      if (ok && msg && msg.ok) {
        saveUserSec(this.secret)
        this.setState({ dialogMsg: '成功,等待机器人验证(大约一分钟)', success: true })
      } else {
        if (msg && !msg.ok && '1001' === msg.code) {
          this.setState({ dialogMsg: '账户余额不足。请充值或去朋友圈领取现金红包。', renderDesposit: true })
        } else
          this.setState({ dialogMsg: '失败' })
      }
    }).bind(this))
  }

  doWinnerConfirm() {
    let transactionId = this.state.product.transactionId;
    let peer = createPeerId(this.secret);
    //check peer is winner
    let data = {
      action: 'transaction', type: 'sAction', accountId: peer.aid,
      fee: this.fee, dealine: this.deadline, sid: transactionId, saction: '3'
    };
    doBShopTransaction(this.secret, data, ((ok, msg) => {
      if (ok && msg && msg.ok) {
        saveUserSec(this.secret)
        this.setState({ dialogMsg: '成功,等待机器人验证(大约一分钟)', success: true })
      } else {
        if (msg && !msg.ok && '1001' === msg.code) {
          this.setState({ dialogMsg: '账户余额不足。请充值或去朋友圈领取现金红包。', renderDesposit: true })
        } else
          this.setState({ dialogMsg: '失败' })
      }
    }).bind(this))
  }

  toggleConfirm() {
    this.setState({
      confirm: !this.state.confirm, dialogMsg: ''
    });
  }

  renderDropDialog() {
    let winnerId = this.state.winner.accountId
    return (<Modal isOpen={this.state.drop} toggle={this.toggleDrop.bind(this)}
      className={'modal-info ' + this.props.className}>
      <ModalHeader toggle={this.toggleDrop.bind(this)}>{winnerId}放弃交易</ModalHeader>
      <ModalBody>
        <p>物品: {this.state.product.title}</p>
        <p>卖家原价:{this.state.product.price} 积分</p>
        <FormGroup>
          <div className="controls">
            <InputGroup>
              <BtbSecret onSecret={this.onSecret.bind(this)} />
            </InputGroup>
          </div>
        </FormGroup>
        {this.renderFeeAndDeadline(1)}
        <FormGroup>
          <Alert color="danger"><small>买家放弃后账户自动退回抢购金额,卖家退回保证金.</small></Alert>
        </FormGroup>
        <FormGroup>
          {this.renderdialogMsg()}
        </FormGroup>
      </ModalBody>
      <ModalFooter>
        <Button color="danger" onClick={this.toggleDrop.bind(this)}>取消</Button>
        <Button color="danger" hidden={this.state.success} onClick={this.doDrop.bind(this)}>确认放弃</Button>
      </ModalFooter>
    </Modal>)
  }

  renderRejectConfirmDialog() {
    let senderId = this.state.product.senderId;
    return (<Modal isOpen={this.state.rejectConfirm} toggle={this.toggleRejectConfirm.bind(this)}
      className={'modal-info ' + this.props.className}>
      <ModalHeader toggle={this.toggleRejectConfirm.bind(this)}>{senderId}确认收到退货</ModalHeader>
      <ModalBody>
        <p>物品: {this.state.product.title}</p>
        <p>卖家原价:{this.state.product.price} 积分</p>
        <FormGroup>
          <div className="controls">
            <InputGroup>
              <BtbSecret onSecret={this.onSecret.bind(this)} />
            </InputGroup>
          </div>
        </FormGroup>
        {this.renderFeeAndDeadline()}
        <FormGroup>
          <Alert color="danger"><small>确认后卖家将立即收到保证金和系统补偿.交易结束.双方可以对交易评价</small></Alert>
        </FormGroup>
        <FormGroup>
          {this.renderdialogMsg()}
        </FormGroup>
      </ModalBody>
      <ModalFooter>
        <Button color="danger" onClick={this.toggleRejectConfirm.bind(this)}>取消</Button>
        <Button color="danger" hidden={this.state.success} onClick={this.doRejectConfirm.bind(this)}>确认退货</Button>
      </ModalFooter>
    </Modal>)
  }

  renderIssueTransactions() {
    let issues = this.state.issues;
    if (!issues)
      return null;
    let content = issues.map(((res) => {
      let btime = moment(res.time).format('MM-DD HH:mm')
      let color = 'black'
      if (res.luckey) {
        color = 'red';
      }
      return (<tr>
        <td><AccountDialog rsId={res.from} /></td>
        <td style={{ color: color }}>{res.wp}%</td>
        <td style={{ color: color }}>{res.sp}%</td>
        <td>{new Buffer(res.info, 'base64').toString()}</td>
        <td>{btime}</td>
      </tr>)
    }).bind(this));

    return content;
  }

  copyAddress() {
    let addr = document.querySelector('#invite_vote_url');
    addr.focus()
    addr.select()
    var successful = document.execCommand('copy');
    let msg = document.querySelector('#msg_invite');
    if (successful) {
      msg.innerHTML = 'OK.复制成功'
    } else {
      msg.innerHTML = '复制失败，请手动复制'
    }
  }

  renderInviteVotes() {
    let winnerId = this.state.winner.accountId
    let senderId = this.state.product.senderId;
    let link = getCurrentHost() + "/#/bshop/product-list#" + this.state.product.transactionId + '#.我在网上购物出现纠纷，请打开这个地址帮忙投一票解决交易纠纷,挽回我的损失。谢谢';

    return (<Modal isOpen={this.state.voteHelp} toggle={this.toggleHelp.bind(this)}
      className={'modal-info ' + this.props.className}>
      <ModalHeader toggle={this.toggleHelp.bind(this)}>邀请好友帮忙投票</ModalHeader>
      <ModalBody>
        <p>请复制下面链接发送给好友帮忙投票</p>
        <p><Input type='textarea' rows='2' id='invite_vote_url' value={link}></Input></p>
        <Button color="ghost-success" onClick={(e) => this.copyAddress()}>
          <i className="fa fa-copy fa-lg"></i>&nbsp;复制地址
        </Button>
        <span id='msg_invite'></span>
      </ModalBody>
      <ModalFooter>
        <Button color="danger" onClick={this.toggleHelp.bind(this)}>取消</Button>
      </ModalFooter>
    </Modal>)
  }

  renderIssueVotes() {
    let winnerId = this.state.winner.accountId
    let senderId = this.state.product.senderId;
    return (<Modal isOpen={this.state.votes} toggle={this.toggleVotes.bind(this)}
      className={'modal-info ' + this.props.className}>
      <ModalHeader toggle={this.toggleVotes.bind(this)}>纠纷投票列表</ModalHeader>
      <ModalBody>
        <p>物品: {this.state.product.title}</p>
        <p>卖家原价:{this.state.product.price} 积分</p>
        <p>卖家账号:<AccountDialog rsId={senderId} /></p>
        <p>买家账号:<AccountDialog rsId={winnerId} /></p>
        <p>纠纷原因:{getIssueReason(this.state.product.issueReason)}</p>
        <Table hover bordered striped responsive size="sm">
          <thead>
            <tr>
              <th>投票账户</th>
              <th>买家%</th>
              <th>卖家%</th>
              <th>说明</th>
              <th>时间</th>
            </tr>
          </thead>
          <tbody>
            {this.renderIssueTransactions()}
          </tbody>
          <Pagination>
            <PaginationItem><PaginationLink previous tag="button" onClick={(e) => { this.preIssuePage() }}>-</PaginationLink></PaginationItem>
            <PaginationItem active>
              <PaginationLink tag="button">{this.state.issuePage}</PaginationLink>
            </PaginationItem>
            <PaginationItem><PaginationLink next tag="button" onClick={(e) => { this.nextIssuePage() }}>+</PaginationLink></PaginationItem>
          </Pagination>
        </Table>
      </ModalBody>
      <ModalFooter>
        <Button color="danger" onClick={this.toggleVotes.bind(this)}>取消</Button>
        <Button color="danger" hidden={this.state.success} onClick={this.toggleIssue.bind(this)}>投票</Button>
      </ModalFooter>
    </Modal>)
  }


  preIssuePage() {
    this.state.issuePage--
    loadIssueTransactions(this.state.product, this.state.issuePage, 6, (ok, issues) => {
      if (ok && issues)
        this.setState({ issues })
    })
  }

  nextIssuePage() {
    this.state.issuePage++
    loadIssueTransactions(this.state.product, this.state.issuePage, 6, (ok, issues) => {
      if (ok && issues)
        this.setState({ issues })
    })
  }


  renderWinnerConfirmedProductDialog() {
    let winnerId = this.state.winner.accountId
    return (<Modal isOpen={this.state.confirm} toggle={this.toggleConfirm.bind(this)}
      className={'modal-info ' + this.props.className}>
      <ModalHeader toggle={this.toggleConfirm.bind(this)}>{winnerId} 确认收货</ModalHeader>
      <ModalBody>
        <p>物品: {this.state.product.title}</p>
        <p>卖家原价:{this.state.product.price} 积分</p>
        <FormGroup>
          <div className="controls">
            <InputGroup>
              <BtbSecret onSecret={this.onSecret.bind(this)} />
            </InputGroup>
          </div>
        </FormGroup>
        {this.renderFeeAndDeadline()}
        <FormGroup>
          <Alert color="danger"><small>确认后卖家将立即收到货款。交易结束.双方可以对交易评价</small></Alert>
        </FormGroup>
        <FormGroup>
          {this.renderdialogMsg()}
        </FormGroup>
      </ModalBody>
      <ModalFooter>
        <Button color="danger" onClick={this.toggleConfirm.bind(this)}>取消</Button>
        <Button color="danger" hidden={this.state.success} onClick={this.doWinnerConfirm.bind(this)}>确认收货</Button>
      </ModalFooter>
    </Modal>)
  }

  renderIssueDialog() {
    let winnerId = this.state.winner.accountId
    let senderId = this.state.product.senderId;
    let totalAmount = (this.state.product.price + " + " + this.state.product.guarantee)
    let t1 = '买家申请纠纷处理'
    let t2 = '卖家申请纠纷处理'
    let t3 = '我要投票解决纠纷'
    //
    return (<Modal isOpen={this.state.issue} toggle={this.toggleIssue.bind(this)}
      className={'modal-info ' + this.props.className}>
      <ModalHeader toggle={this.toggleIssue.bind(this)}>投票解决交易纠纷</ModalHeader>
      <ModalBody>
        <p>商品名称: {this.state.product.title}</p>
        <p>卖家原价:{this.state.product.price} 积分</p>
        <p>卖家保证金:{this.state.product.guarantee} 积分</p>
        <p>卖家账号:<AccountDialog rsId={senderId} /></p>
        <p>买家账号:<AccountDialog rsId={winnerId} /></p>
        <p>纠纷原因:{getIssueReason(this.state.product.issueReason)}</p>
        <FormGroup>
          <div className="controls">
            <InputGroup>
              <BtbSecret onSecret={this.onSecret.bind(this)} />
            </InputGroup>
          </div>
        </FormGroup>
        <FormGroup>
          <Dropdown isOpen={this.state.selectIssueAction} toggle={
            (() => {
              this.setState({
                selectIssueAction: !this.state.selectIssueAction,
              });
            }).bind(this)
          }>
            <DropdownToggle caret>
              {this.state.selectActionTitle}
            </DropdownToggle>
            <DropdownMenu>
              <DropdownItem onClick={(() => {
                this.setState({ selectActionTitle: t1, issueReason: 1 })
              }).bind(this)}>{t1}</DropdownItem>
              <DropdownItem onClick={(() => {
                this.setState({ selectActionTitle: t2, issueReason: 2 })
              }).bind(this)}>{t2}</DropdownItem>
              <DropdownItem onClick={(() => {
                this.setState({ selectActionTitle: t3, issueReason: 3 })
              }).bind(this)}>{t3}</DropdownItem>
            </DropdownMenu>
          </Dropdown>
          <Label htmlFor="appendedInputButtons">交易纠纷总金额 {totalAmount} 的90%分配给买卖双方，10%留做投票奖励:</Label>
          <div className="controls">
            <InputGroup className="input-prepend">
              <InputGroupAddon addonType="prepend">
                <InputGroupText>给卖家投票:</InputGroupText>
              </InputGroupAddon>
              <Input id="appendedPrependedInput" size="16" type="text" onChange={this.updateSellerRefund.bind(this)} placeholder='输入卖家退款百分比，例如50' />
              <InputGroupAddon addonType="append">
                <InputGroupText>%(百分比)</InputGroupText>
              </InputGroupAddon>
            </InputGroup>
            <InputGroup className="input-prepend">
              <InputGroupAddon addonType="prepend">
                <InputGroupText>给买家投票:</InputGroupText>
              </InputGroupAddon>
              <Input id="appendedPrependedInput" size="16" type="text" onChange={this.updateBuyerRefund.bind(this)} placeholder='输入买家退款百分比,例如50' />
              <InputGroupAddon addonType="append">
                <InputGroupText>%(百分比)</InputGroupText>
              </InputGroupAddon>
            </InputGroup>
          </div>
        </FormGroup>

        <FormGroup>
          <div className="controls">
            <InputGroup className="input-prepend">
              <InputGroupAddon addonType="prepend">
                <InputGroupText>说明:</InputGroupText>
              </InputGroupAddon>
              <Input type="textarea" name="textarea-input" id="textarea-input" rows="3" onChange={this.updateIssueNote.bind(this)} placeholder='纠纷意见简单描述.100字以内.' />
            </InputGroup>
          </div>
        </FormGroup>
        {this.renderFeeAndDeadline()}
        <FormGroup>
          <Alert color="danger"><small>交易纠纷信息将公开，所有账户都可以根据公开信息投票帮助解决纠纷。</small></Alert>
          <Alert color="danger"><small>投票者请结合当前交易情况、买卖双方交易证据与当前商品所在地等信息给出客观、公正的投票结果。</small></Alert>
          <Alert color="danger"><small>系统机器人随机选择(5天投票期)一批投票结果计算最终仲裁结果，自动给双方退款。同时对于最接近仲裁结果的投票账户，自动发放纠纷金额的10%作为奖励。</small></Alert>
        </FormGroup>
        <FormGroup>
          {this.renderdialogMsg()}
        </FormGroup>
      </ModalBody>
      <ModalFooter>
        <Button color="danger" onClick={this.toggleIssue.bind(this)}>取消</Button>
        <Button color="danger" hidden={this.state.success} onClick={this.doIssue.bind(this)}>发布</Button>
      </ModalFooter>
    </Modal>)
  }


  renderWinner7dayRefundDialog() {
    let winnerId = this.state.winner.accountId
    return (<Modal isOpen={this.state.day7refund} toggle={this.toggle7dayRefund.bind(this)}
      className={'modal-info ' + this.props.className}>
      <ModalHeader toggle={this.toggle7dayRefund.bind(this)}>{winnerId} - 8天无理由退货</ModalHeader>
      <ModalBody>
        <p>物品: {this.state.product.title}</p>
        <p>卖家原价:{this.state.product.price} 积分</p>
        <FormGroup>
          <Label htmlFor="appendedInputButtons">眞客账户密码:</Label>
          <div className="controls">
            <InputGroup>
              <BtbSecret onSecret={this.onSecret.bind(this)} />
            </InputGroup>
          </div>
        </FormGroup>
        {this.renderFeeAndDeadline(1)}
        <FormGroup>
          <div className="controls">
            <InputGroup className="input-prepend">
              <InputGroupAddon addonType="prepend">
                <InputGroupText>快递信息:</InputGroupText>
              </InputGroupAddon>
              <Input id="prependedInput2" size="16" type="text" onChange={this.updateTracking.bind(this)} placeholder='快递单号:' />
            </InputGroup>
            <InputGroup className="input-prepend">
              <InputGroupAddon addonType="prepend">
                <InputGroupText>信息加密:</InputGroupText>
              </InputGroupAddon>
              <Input type="textarea" name="textarea-input" id="textarea-input" rows="3" value={this.state.trackingBlock} />
              <Alert color="danger"><small>买家收到货后可以与卖家沟通，申请退货。退货快递信息加密后只有卖家可以解密，任何其他人都无法查看</small></Alert>
            </InputGroup>
          </div>
        </FormGroup>
        <FormGroup>
          <Alert color="danger"><small>卖家收到退货后确认，系统自动给买家退款。系统将自动退保证金给卖家。7天未确认系统将自动确认.</small></Alert>
        </FormGroup>
        <FormGroup>
          {this.renderdialogMsg()}
        </FormGroup>
      </ModalBody>
      <ModalFooter>
        <Button color="danger" onClick={this.toggle7dayRefund.bind(this)}>取消</Button>
        <Button color="danger" hidden={this.state.success} onClick={this.do7dayrefund.bind(this)}>无理由退货</Button>
      </ModalFooter>
    </Modal>)
  }

  renderFeeAndDeadline(fee) {
    let fholder = '机器人费，最少0.1积分';
    if (fee) {
      fholder = '机器人费，最少' + fee + '积分';
    }
    return (<div>
      <FormGroup>
        <div className="controls">
          <InputGroup className="input-prepend">
            <InputGroupAddon addonType="prepend">
              <InputGroupText><PopHint id="min123" text='(机器人费:' content='机器人为账户余额比较多的账户，负责验证所有交易是否符合规则,并将合法交易保存到区块链(5分钟中未验证的交易将被丢弃，相当于交易没有发生)。机器人保证整个系统按照规则运行。' /></InputGroupText>
            </InputGroupAddon>
            <Input id="appendedPrependedInput" size="16" type="text" onChange={this.updateFee.bind(this)} placeholder={fholder} />
            <InputGroupAddon addonType="append">
              <InputGroupText>积分</InputGroupText>
            </InputGroupAddon>
          </InputGroup>
        </div>
      </FormGroup>
      {
        this.renderDeadline()
      }
    </div>)
  }

  renderDeadline() {
    if (true)
      return null;
    return (<FormGroup>
      <div className="controls">
        <InputGroup className="input-prepend">
          <InputGroupAddon addonType="prepend">
            <InputGroupText><PopHint id='dl4545' text='(有效期:' content='机器人验证交易时候会按手续费高低排序处理，有效期内未被处理的交易会被舍弃。交易作废。' /></InputGroupText>
          </InputGroupAddon>
          <Input id="appendedPrependedInput" size="16" onChange={this.updateDeadline.bind(this)} type="text" placeholder='交易有效期，默认5分钟' />
          <InputGroupAddon addonType="append">
            <InputGroupText>分钟</InputGroupText>
          </InputGroupAddon>
        </InputGroup>
      </div>
    </FormGroup>)
  }


  doDecrypt() {
    if (!this.state.infoBlocks)
      return null;
    if (!this.secret) {
      this.setState({ dialogMsg: '(未登录)' })
      return;
    }
    let decrypted = [];
    this.state.infoBlocks.forEach(((e) => {
      let clear;
      if (e.type > 0)
        clear = shareDecrypt(this.secret, e.pk, e.id, e.block);
      else
        clear = new Buffer(e.block, 'base64').toString()
      decrypted.push(clear);
    }).bind(this))
    this.setState({ decrypted })
  }

  renderEncryptedInfos() {
    if (!this.state.infoBlocks)
      return null;
    let idx = 0;
    let content = this.state.infoBlocks.map(((rec) => {
      let txt = rec.block;
      if (this.state.decrypted) {
        txt = this.state.decrypted[idx];
      }
      idx++;
      return (<div className="controls">
        <InputGroup className="input-prepend">
          <InputGroupAddon addonType="prepend">
            <InputGroupText>密文:</InputGroupText>
          </InputGroupAddon>
          <Input type="textarea" name="textarea-input" id="textarea-input" rows="3" value={txt} />
        </InputGroup>
      </div>)
    }).bind(this))
    return content;
  }





  renderDecryptDialog() {
    return (<Modal isOpen={this.state.decrypt} toggle={this.toggleDecrypt.bind(this)}
      className={'modal-info ' + this.props.className}>
      <ModalHeader toggle={this.toggleDecrypt.bind(this)}>信息解密</ModalHeader>
      <ModalBody>
        <p>物品: {this.state.product.title}</p>
        <p>卖家原价:{this.state.product.price} 积分</p>
        <FormGroup>
          <div className="controls">
            <InputGroup>
              <BtbSecret onSecret={this.onSecret.bind(this)} />
            </InputGroup>
          </div>
        </FormGroup>
        <FormGroup>
          {this.renderEncryptedInfos()}
        </FormGroup>
        <FormGroup>
          <Alert color="danger"><small>交易信息已经加密，只有买卖双方才可以解密。</small></Alert>
        </FormGroup>
        <FormGroup>
          {this.renderdialogMsg()}
        </FormGroup>
      </ModalBody>
      <ModalFooter>
        <Button color="danger" onClick={this.toggleDecrypt.bind(this)}>取消</Button>
        <Button color="danger" onClick={this.doDecrypt.bind(this)}>解密信息</Button>
      </ModalFooter>
    </Modal>)
  }

  renderDeliverProductDialog() {
    let senderId = this.state.product.senderId;
    return (<Modal isOpen={this.state.sending} toggle={this.toggleSending.bind(this)}
      className={'modal-info ' + this.props.className}>
      <ModalHeader toggle={this.toggleSending.bind(this)}>{senderId} 发货</ModalHeader>
      <ModalBody>
        <p>物品: {this.state.product.title}</p>
        <p>卖家原价:{this.state.product.price} 积分</p>
        <FormGroup>
          <Label htmlFor="appendedInputButtons">眞客账户密码:</Label>
          <div className="controls">
            <InputGroup>
              <BtbSecret onSecret={this.onSecret.bind(this)} />
            </InputGroup>
          </div>
        </FormGroup>
        {this.renderFeeAndDeadline(1)}
        <FormGroup>
          <div className="controls">
            <InputGroup className="input-prepend">
              <InputGroupAddon addonType="prepend">
                <InputGroupText>快递信息:</InputGroupText>
              </InputGroupAddon>
              <Input id="prependedInput2" size="16" type="text" onChange={this.updateTracking.bind(this)} placeholder='快递单号:' />
            </InputGroup>
            <InputGroup className="input-prepend">
              <InputGroupAddon addonType="prepend">
                <InputGroupText>信息加密:</InputGroupText>
              </InputGroupAddon>
              <Input type="textarea" name="textarea-input" id="textarea-input" rows="3" value={this.state.trackingBlock} />
            </InputGroup>
          </div>
        </FormGroup>
        <FormGroup>
          <Alert color="danger"><small>快递信息加密后只有买家可以解密，任何其他人都无法查看</small></Alert>
          <Alert color="danger"><small>发货后等待买家收货，买家确认后系统将立即付款（含保证金）给卖家。</small></Alert>
        </FormGroup>
        <FormGroup>
          {this.renderdialogMsg()}
        </FormGroup>
      </ModalBody>
      <ModalFooter>
        <Button color="danger" onClick={this.toggleSending.bind(this)}>取消</Button>
        <Button color="danger" hidden={this.state.success} onClick={this.doSending.bind(this)}>发货</Button>
      </ModalFooter>
    </Modal>)
  }

  renderPayingProductDialog() {
    if (!this.state.product)
      return null;
    let winner = this.state.winner;
    let pwdHint = '';
    let payId = ''
    if (winner) {
      pwdHint = "输入" + winner.accountId + "的密码"
      payId = winner.accountId
    }

    return (<Modal isOpen={this.state.paying} toggle={this.togglePay10p.bind(this)}
      className={'modal-info ' + this.props.className}>
      <ModalHeader toggle={this.togglePay10p.bind(this)}>{payId}买家发送收货地址</ModalHeader>
      <ModalBody>
        <p>物品: {this.state.product.title}</p>
        <p>卖家原价:{this.state.product.price} 积分</p>
        <p>卖家保证金:{this.state.product.guarantee} 积分</p>
        <p>抢购价:{(this.state.product.price / 10)} 积分</p>
        <FormGroup>
          <Label htmlFor="appendedInputButtons">眞客账户密码:</Label>
          <div className="controls">
            <InputGroup>
              <BtbSecret onSecret={this.onSecret.bind(this)} />
            </InputGroup>
          </div>
        </FormGroup>
        {this.renderFeeAndDeadline(1)}
        <FormGroup>
          <div className="controls">
            <InputGroup className="input-prepend">
              <InputGroupAddon addonType="prepend">
                <InputGroupText>收货人:</InputGroupText>
              </InputGroupAddon>
              <Input id="prependedInput2" size="16" type="text" onChange={this.updateContact.bind(this)} placeholder='(收货人)' />
            </InputGroup>
            <InputGroup className="input-prepend">
              <InputGroupAddon addonType="prepend">
                <InputGroupText>电话:</InputGroupText>
              </InputGroupAddon>
              <Input id="prependedInput2" size="16" type="text" onChange={this.updatePhone.bind(this)} placeholder='(电话)' />
            </InputGroup>
            <InputGroup className="input-prepend">
              <InputGroupAddon addonType="prepend">
                <InputGroupText>收货地址:</InputGroupText>
              </InputGroupAddon>
              <Input id="prependedInput2" size="16" type="text" onChange={this.updateAddress.bind(this)} placeholder='(收货地址)' />
            </InputGroup>
            <InputGroup className="input-prepend">
              <InputGroupAddon addonType="prepend">
                <InputGroupText>加密地址:</InputGroupText>
              </InputGroupAddon>
              <Input type="textarea" name="textarea-input" id="textarea-input" rows="3" value={this.state.addressBlock} />
            </InputGroup>
          </div>
        </FormGroup>
        <FormGroup>
          <Alert color="danger"><small>保护用户隐私。收货地址加密后只有卖家可以解密，任何其他人都无法查看</small></Alert>
          <Alert color="danger"><small>买家付款三天后卖家未发货，买家账户将自动全部退款并补偿商品保证金。</small></Alert>
        </FormGroup>
        <FormGroup>
          {this.renderdialogMsg()}
        </FormGroup>
      </ModalBody>
      <ModalFooter>
        <Button color="danger" onClick={this.togglePay10p.bind(this)}>取消</Button>
        {this.renderPayActionButton()}
      </ModalFooter>
    </Modal>)
  }

  renderPayActionButton() {
    if (this.state.success)
      return null;
    return (<Button color="danger" onClick={this.doPaying.bind(this)}>发送地址给卖家</Button>);
  }

  render() {
    return (<div>
      {this.renderActionButtons()}
      {this.renderPayingProductDialog()}
      {this.renderDropDialog()}
      {this.renderDeliverProductDialog()}
      {this.renderWinnerConfirmedProductDialog()}
      {this.renderWinner7dayRefundDialog()}
      {this.renderIssueDialog()}
      {this.renderRejectConfirmDialog()}
      {this.renderIssueVotes()}
      {this.renderInviteVotes()}
      {this.renderDecryptDialog()}
    </div>)
  }
}
export default ActionButton;
