import React, { Component } from 'react';
import { Badge, Card, CardBody, CardHeader, Col, Pagination, PaginationItem, PaginationLink, Row, Table } from 'reactstrap';
import { Button, Input, InputGroup, InputGroupAddon, InputGroupText } from 'reactstrap';
import { Popover, PopoverHeader, PopoverBody, DropdownItem, FormGroup, Collapse, Label } from 'reactstrap'
import { Modal, ModalBody, ModalFooter, ModalHeader } from 'reactstrap'
import { loadLocalAccount, saveUserSec, createPeerId, calcAccountId, getFileHost, getCurrentHost } from '../../ggv/js/gegeUtil'
import { Nav, NavItem, NavLink, Alert, Breadcrumb, BreadcrumbItem, ListGroup, ListGroupItem } from 'reactstrap';
import classnames from 'classnames';
import ReactQuill from 'react-quill';
import 'react-quill/dist/quill.snow.css';
import AccountDialog from '../../conponents/AccountDialog'
import PopHint from '../../conponents/PopHint/PopHint'
import BtbSecret from '../../conponents/BtbSecret/BtbSecret'
import ActionButton from '../../conponents/ActionButton/ActionButton'
import DepositDialog from '../../conponents/DepositDialog/DepositDialog'
import ImgDialog from '../../conponents/ImgDialog'
const get = require('simple-get')
const { getTorrentHash, loadProductList, loadProduct, doBShopTransaction, getProductStatus, loadProductDetails, loadJoinTransactions, loadIssueTransactions, shareEncrypt, getProductNextStep } = require('../eshop.js')
const moment = require('moment')
class ProductList extends Component {

  constructor(props) {
    super(props);
    this.deadline = 5;
    this.fee = '1';
    this.guarantee = '2'
    this.state = {
      account: {}, list: [], page: 1, joinsPage: 1, addProduct: false, join: false, detailProduct: null,
      detail: false, paying: null, activeTab: '0', detailTab: '0', joins: null, showAccount: false,
      content: '在此编辑物品的详细介绍，所在城市，联系方式等信息.', filterValue: null, success: false, issuePage: 1,
      product: {}
    }
    this.modules = {
      toolbar: [
        [{ 'header': '1' }, { 'header': '2' }, { 'font': [] }],
        [{ size: [] }], [{ align: [] }, 'direction'],
        ['bold', 'italic', 'underline', 'strike', 'blockquote', 'code-block'],
        [{ color: [] }, { background: [] }],
        [{ 'list': 'ordered' }, { 'list': 'bullet' },
        { 'indent': '-1' }, { 'indent': '+1' }],
        ['link', 'image'],
        ['clean']
      ],
      clipboard: {
        // toggle to add extra line breaks when pasting HTML:
        matchVisual: false,
      }
    }
  }

  componentDidMount() {
    console.log('plist did mount');
    var account = loadLocalAccount();
    if (account && account.secret)
      this.secret = account.secret;
    this.setState({ account })
    let hash = window.location.hash;
    let query = null;
    if (hash) {
      let idx = hash.indexOf("#", 1);
      if (idx > 0) {
        query = hash.substr(idx + 1, 40);
        idx = query.indexOf("#", 1);
        if (idx > 0) {
          query = query.substr(0, idx);
        }
        //console.log('query=' + query);
      }
    }
    setTimeout((() => {
      if (query)
        loadProduct(this.onProductList.bind(this), query)
      else
        loadProductList(this.onProductList.bind(this), 1, this.state.activeTab)
    }).bind(this), 500);
    //
    //this.doTest()
  }

  componentWillUnmount() {
    console.log('plist will unmount');
  }

  onProductList(ok, list) {
    if (ok) {
      this.setState({ list })
    } else {
      this.setState({ list: [] })
    }
  }

  prePage() {
    if (this.state.page > 1) {
      this.state.page--
      loadProductList(this.onProductList.bind(this), this.state.page, this.state.activeTab, '0', this.state.filterValue)
    }
  }

  nextPage() {
    this.state.page++
    loadProductList(this.onProductList.bind(this), this.state.page, this.state.activeTab, '0', this.state.filterValue)
  }

  filterBlocks() {
    this.setState({ page: 1 })
    loadProductList(this.onProductList.bind(this), 1, this.state.activeTab, '0', this.state.filterValue)
  }

  togglePublishProduct() {
    this.setState({
      addProduct: !this.state.addProduct, success: false
    });
  }

  toggleJoin() {
    this.setState({
      join: !this.state.join, joinsPage: 1, success: false, dialogMsg: ''
    });
    //set product to null
  }

  toggleDetail() {
    this.setState({
      detail: !this.state.detail,
      issues: null, joins: null,
      detailTab: '0', issuePage: 1
    });
  }


  onSecret(sec) {
    if (sec) {
      this.secret = sec
      let rsId = calcAccountId(this.userSec);
      this.setState({ rsId: rsId })
    }
  }

  updateFee(e) {
    if (e && e.target) {
      this.fee = e.target.value;
      if (this.fee.length == 0) {
        this.fee = 1;
      }
    }
  }

  updatePrice(e) {
    if (e && e.target) {
      this.price = e.target.value;
    }
  }
  updateExpire(e) {
    if (e && e.target) {
      this.expire = e.target.value;
    }
  }
  updateQuality(e) {
    if (e && e.target) {
      this.quality = e.target.value;
    }
  }

  updateGuarantee(e) {
    if (e && e.target) {
      this.guarantee = e.target.value;
    }
  }

  updateTags(e) {
    if (e && e.target) {
      this.tags = e.target.value;
    }
  }

  updateDeadline(e) {
    if (e && e.target) {
      this.deadline = e.target.value;
      if (this.deadline.length == 0) {
        this.deadline = 5;
      }
    }
  }
  updateTitle(e) {
    if (e && e.target) {
      this.title = e.target.value;
    }
  }

  updateAddress(e) {
    if (e && e.target) {
      this.address = e.target.value;
      this.updateAddressBlock()
    }
  }

  updateContact(e) {
    if (e && e.target) {
      this.contact = e.target.value;
      this.updateAddressBlock()
    }
  }
  updatePhone(e) {
    if (e && e.target) {
      this.phone = e.target.value;
      this.updateAddressBlock()
    }
  }

  updateAddressBlock() {
    let clear = { contract: this.contact, address: this.address, phone: this.phone };
    clear = JSON.stringify(clear);
    let pk = this.state.productDetail.seller;
    let addressBlock = shareEncrypt(this.secret, pk, this.state.product.transactionId, clear);
    let addressHash = ''
    this.setState({ addressBlock: addressBlock, addressHash: addressHash });
  }



  doJoin() {
    if (!this.joinAmount) {
      this.setState({ dialogMsg: '抢购价格错误' })
      return;
    }
    let transactionId = this.state.product.transactionId;
    let peer = createPeerId(this.secret);
    let data = {
      action: 'transaction', type: 'sjoin', amount: this.joinAmount,
      fee: this.fee, price: this.price, dealine: this.deadline, sid: transactionId, pk: peer.pk, accountId: peer.aid
    };
    doBShopTransaction(this.secret, data, ((ok, msg) => {
      if (ok && msg && msg.ok) {
        saveUserSec(this.secret)
        this.setState({ dialogMsg: '成功,等待机器人验证(大约一分钟).', success: true })
      } else {
        if (msg && !msg.ok && '1001' === msg.code) {
          this.setState({ dialogMsg: '账户余额不足。请充值或去朋友圈领取现金红包。', renderDesposit: true })
        } else
          this.setState({ dialogMsg: '失败' })
      }
    }).bind(this))
  }

  uploadThumbnail(productId) {
    let b64 = this.state.avatar;
    let url = getFileHost() + '/upthumbnail/' + productId + '/token1'
    //url = 'http://127.0.0.1:18080/info'
    var opts = {
      url: url,
      body: b64,
      json: false
    };
    get.concat(opts, function (err, res, data) {

    });
  }

  publishProduct() {
    if (!this.price || parseInt(this.price) < 10) {
      this.setState({ dialogMsg: '商品价格最小为10积分' })
      return;
    }
    //
    let product = {
      tags: this.tags, title: this.title,//
      address: '', content: '',//
      quality: 0
    };
    let peer = createPeerId(this.secret);
    let str = JSON.stringify(product)
    let jsonHex = new Buffer(str).toString('base64');
    let content = this.state.content
    content = '<html><head><meta charset="UTF-8"><title>' + this.title + '</title></head><p>' + content + '</p></html>';
    let digest = getTorrentHash(content);
    let data = {
      action: 'transaction', type: 'shopshare', productJson: jsonHex, guarantee: this.guarantee,
      fee: this.fee, price: this.price, expire: this.expire, dealine: this.deadline, pk: peer.pk,
      accountId: peer.aid, productDetail: null, digest
    };
    //
    doBShopTransaction(this.secret, data, ((ok, msg) => {
      if (ok && msg && msg.ok) {
        saveUserSec(this.secret)
        if (this.state.avatar) {
          this.uploadThumbnail(msg.productId)
        }
        this.uploadNews(msg.productId, content)
        //this.togglePublishProduct()
        let url = getCurrentHost() + "/#/bshop/product-list#" + msg.transactionId;
        this.setState({ dialogMsg: '发布成功,机器人验证后(大约一分钟)即可抢购.', success: true })
      } else {
        if (msg && !msg.ok && '1001' === msg.code) {
          this.setState({ dialogMsg: '账户余额不足。请充值或去朋友圈领取现金红包。', renderDesposit: true })
        } else
          this.setState({ dialogMsg: '失败' })
      }
    }).bind(this))
  }

  uploadNews(newsId, content) {
    console.log('upload news not done yet');
    let b64 = Buffer.from(content).toString('base64');
    let url = getFileHost() + '/uphtml/' + newsId + '/token1'
    //url = 'http://127.0.0.1:18080/info'
    var opts = {
      url: url,
      body: b64,
      json: false
    };
    get.concat(opts, function (err, res, data) {

    });
  }

  handleChange(value) {
    this.setState({ content: value })
  }

  rederAddProductDialog() {
    return (<Modal isOpen={this.state.addProduct} toggle={this.togglePublishProduct.bind(this)}
      className={'modal-info ' + this.props.className}>
      <ModalHeader toggle={this.togglePublishProduct.bind(this)}>出售物品</ModalHeader>
      <ModalBody>
        <FormGroup>
          <div className="controls">
            <BtbSecret onSecret={this.onSecret.bind(this)} />
          </div>
        </FormGroup>
        <FormGroup>
          <div className="controls">
            <InputGroup className="input-prepend">
              <InputGroupAddon addonType="prepend">
                <InputGroupText>物品名称:</InputGroupText>
              </InputGroupAddon>
              <Input id="prependedInput2" size="16" type="text" onChange={this.updateTitle.bind(this)} placeholder='物品名称' />
            </InputGroup>
          </div>
        </FormGroup>
        <FormGroup>
          <div className="controls">
            <InputGroup className="input-prepend">
              <InputGroupAddon addonType="prepend">
                <InputGroupText>物品标签:</InputGroupText>
              </InputGroupAddon>
              <Input id="prependedInput2" size="16" type="text" onChange={this.updateTags.bind(this)} placeholder='商品关键字，方便搜索' />
            </InputGroup>
          </div>
        </FormGroup>
        <FormGroup>
          <div className="controls">
            <InputGroup className="input-prepend">
              <InputGroupAddon addonType="prepend">
                <InputGroupText><PopHint id="min123" text='价格:' content='物品包邮价格，最低10积分。发布后买家以商品价格一折起加价抢购,价格高者抢购成功几率高，成交价低于包邮价,系统为卖家补齐差价,高于包邮价全部归卖家。所有商品8天无理由退货，退货后系统机器人自动补偿卖家固定金额。' /></InputGroupText>
              </InputGroupAddon>
              <Input id="appendedPrependedInput3" size="16" type="text" onChange={this.updatePrice.bind(this)} placeholder='物品包邮价格，最低10积分' />
              <InputGroupAddon addonType="append">
                <InputGroupText>积分</InputGroupText>
              </InputGroupAddon>
            </InputGroup>
          </div>
        </FormGroup>
        <FormGroup>
          <div className="controls">
            <InputGroup className="input-prepend">
              <InputGroupAddon addonType="prepend">
                <InputGroupText><PopHint id="min123" text='保证金:' content='卖家保证金，最小2积分，保证交易真实可靠。保证金有助于提高买家抢购积极性。交易结束自动退回卖家。15天无人购买，则交易关闭，系统机器人自动退回保证金.' /></InputGroupText>
              </InputGroupAddon>
              <Input id="appendedPrependedInput3" size="16" type="text" onChange={this.updateGuarantee.bind(this)} placeholder='保证金，交易结束后自动退回' />
              <InputGroupAddon addonType="append">
                <InputGroupText>积分</InputGroupText>
              </InputGroupAddon>
            </InputGroup>
          </div>
        </FormGroup>
        <FormGroup>
          <div className="controls">
            <InputGroup className="input-prepend">
              <InputGroupAddon addonType="prepend">
                <InputGroupText><PopHint id="min123" text='机器人费:' content='系统机器人负责验证所有交易是否符合固定规则,并将合法交易保存到区块链。机器人保证整个系统按照规则运行,同时获得机器人费。' /></InputGroupText>
              </InputGroupAddon>
              <Input id="appendedPrependedInput" size="16" type="text" onChange={this.updateFee.bind(this)} placeholder='机器人费，最少0.1积分' />
              <InputGroupAddon addonType="append">
                <InputGroupText>积分</InputGroupText>
              </InputGroupAddon>
            </InputGroup>
          </div>
        </FormGroup>
        {
          this.renderDeadline()
        }
        <FormGroup>
          <ImgDialog title='添加商品标题缩略图' onFinish={(img) => {
            //// "data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAUAAAAFCAYAAACNby
            if (img) {
              let sp = img.split(';base64,');
              let avatar = sp[1];
              this.setState({ avatar })
            }
          }}></ImgDialog>
        </FormGroup>
        <FormGroup>
          <Label htmlFor="appendedInputButtons">商品介绍:</Label>
          <Alert><small>简单文字描述，内容太多会影响处理速度.提交后不可修改</small></Alert>
          <ReactQuill value={this.state.content}
            modules={this.modules}
            onChange={this.handleChange.bind(this)} />
        </FormGroup>
        <FormGroup>
          {this.renderdialogMsg()}
        </FormGroup>
      </ModalBody>
      <ModalFooter>
        <Button color="danger" onClick={this.togglePublishProduct.bind(this)}>关闭</Button>
        <Button color="danger" hidden={this.state.success} onClick={
          ((e) => {
            let dialogMsg = "发布中,有大图片可能需要几分钟";
            this.setState({ dialogMsg })
            setTimeout(() => {
              this.publishProduct()
            }, 200);
          }).bind(this)
        }>发布</Button>
      </ModalFooter>
    </Modal>)
  }

  renderDeadline() {
    if (true)
      return null;
    return (<FormGroup>
      <div className="controls">
        <InputGroup className="input-prepend">
          <InputGroupAddon addonType="prepend">
            <InputGroupText><PopHint id='dl4545' text='交易有效期:' content='机器人验证交易时候会按手续费高低排序处理，有效期内未被处理的交易会被舍弃。交易作废。' /></InputGroupText>
          </InputGroupAddon>
          <Input id="appendedPrependedInput" size="16" onChange={this.updateDeadline.bind(this)} type="text" placeholder='交易有效期，默认5分钟' />
          <InputGroupAddon addonType="append">
            <InputGroupText>分钟</InputGroupText>
          </InputGroupAddon>
        </InputGroup>
      </div>
    </FormGroup>)
  }

  renderdialogMsg() {
    if (this.state.renderDesposit) {
      return (<div>
        <Label>{this.state.dialogMsg}</Label>
        <DepositDialog />
      </div>)
    } else
      if (this.state.dialogMsg) {
        return (<Label>{this.state.dialogMsg}</Label>)
      }
    return null;
  }

  setFilter(idx) {
    this.setState({ page: 1, activeTab: idx, filterOption: this.filterMsg[idx] })
  }

  upateFilterValue(e) {
    if (e && e.target) {
      let value = e.target.value;
      if (value.length > 0) {
        value = new Buffer(value).toString('base64')
        this.setState({ filterValue: value })
      } else {
        loadProductList(this.onProductList.bind(this), 1, this.state.activeTab)
      }
    }
  }

  updateJoinAmount(e) {
    if (e && e.target) {
      this.joinAmount = e.target.value;
    }
  }

  renderFilterOptions() {
    let ops = this.filterOptions.map((op, index) => {
      return (<DropdownItem onClick={(e) => {
        this.setFilter(index)
      }}>{op}</DropdownItem>)
    })
    return ops;
  }

  joinProduct(product) {
    this.setState({ product })
    this.toggleJoin()
  }

  showDetailDialog(product) {
    this.setState({ product })
    this.toggleDetail()
    setTimeout((() => {
      loadProductDetails(product, (ok, productDetail) => {
        if (ok && productDetail) {
          this.setState({ productDetail })
          let joins = productDetail.joins;
          if (joins)
            this.setState({ joins })
        }
      })
    }).bind(this), 500);
  }


  preJoinsPage() {
    if (this.state.joinsPage > 1) {
      this.state.joinsPage--
    }
    loadJoinTransactions(this.state.product, this.state.joinsPage, 6, (ok, joins) => {
      if (ok && joins)
        this.setState({ joins })
    })
  }

  nextJoinsPage() {
    this.state.joinsPage++
    loadJoinTransactions(this.state.product, this.state.joinsPage, 6, (ok, joins) => {
      if (ok && joins)
        this.setState({ joins })
    })
  }

  onDetailTabClicked(tab) {
    if (this.state.detailTab !== tab) {
      this.setState({
        detailTab: tab,
      });
      //
      if (tab === '0') {
        //load details
      }
      if (tab === '1') {
        //load joins transaction
        loadJoinTransactions(this.state.product, this.state.joinsPage, 6, (ok, joins) => {
          if (ok && joins)
            this.setState({ joins })
        })
      } if (tab === '2') {
        //load joins transaction
        loadIssueTransactions(this.state.product, this.state.issuePage, 6, (ok, issues) => {
          if (ok && issues)
            this.setState({ issues })
        })
      }
    }
  }

  renderJoinTransactions() {
    let joins = this.state.joins;
    if (!joins)
      return null;
    let content = joins.map(((res) => {
      let btime = moment(res.time).format('MM-DD HH:mm')
      let info = res.winner ? <span style={{ color: 'red' }}>抢购成功</span> : '等待退款';
      if (this.state.product.status > 2) {
        info = res.winner ? <span style={{ color: 'red' }}>抢购成功</span> : '已退款';
        if (this.state.product.status == 11) {
          info = '已退款';
        }
      }
      return (<tr>
        <td><PopHint id={'tamts'} text={info} content='抢购关闭之前结果为临时计算结果。如果有新的抢购者加入，抢购成功账户会重新计算.' /></td>
        <td><AccountDialog rsId={res.accountId} /></td>
        <td>{res.amount}积分</td>
        <td>{btime}</td>
      </tr>)
    }).bind(this));

    return content;
  }

  renderProductTabs() {
    if (this.state.detailTab === '0') {
      let productId = this.state.productDetail.product.productId;
      let url = getFileHost() + '/html/0/' + productId;
      return (<iframe width='100%' height='100%' frameborder='0' name="_blank" src={url}></iframe>)
      /*
      //quill
      let content;
      if (this.state.productDetail.product.json && this.state.productDetail.product.json.content)
        content = this.state.productDetail.product.json.content;
      if (content) {
        content = sanitizeHtml(content, {
          allowedTags: sanitizeHtml.defaults.allowedTags.concat(['img']),
          allowedAttributes: {
            'img': ['src']
          },
          allowedSchemes: ['data', 'http']
        });
        return (<ReactQuill value={content} />)
      }
      */
    }

    if (this.state.detailTab === '1') {
      //join transactions
      return (<div><Table hover bordered striped responsive size="sm">
        <tbody>
          {this.renderJoinTransactions()}
        </tbody>
      </Table>
        <Pagination>
          <PaginationItem><PaginationLink previous tag="button" onClick={(e) => { this.preJoinsPage() }}>-</PaginationLink></PaginationItem>
          <PaginationItem active>
            <PaginationLink tag="button">{this.state.joinsPage}</PaginationLink>
          </PaginationItem>
          <PaginationItem><PaginationLink next tag="button" onClick={(e) => { this.nextJoinsPage() }}>+</PaginationLink></PaginationItem>
        </Pagination>
      </div>)
    }
    if (this.state.detailTab === '2') {
      //issue transactions
      return (<Table hover bordered striped responsive size="sm">
        <thead>
          <tr>
            <th>投票账户</th>
            <th>买家%</th>
            <th>卖家%</th>
            <th>时间</th>
            <th>说明</th>
          </tr>
        </thead>
        <tbody>
          {this.renderIssueTransactions()}
        </tbody>
        <Pagination>
          <PaginationItem><PaginationLink previous tag="button" onClick={(e) => { this.preIssuePage() }}>-</PaginationLink></PaginationItem>
          <PaginationItem active>
            <PaginationLink tag="button">{this.state.issuePage}</PaginationLink>
          </PaginationItem>
          <PaginationItem><PaginationLink next tag="button" onClick={(e) => { this.nextIssuePage() }}>+</PaginationLink></PaginationItem>
        </Pagination>
      </Table>)
    }
  }

  preIssuePage() {
    this.state.issuePage--
    loadIssueTransactions(this.state.product, this.state.issuePage, 6, (ok, issues) => {
      if (ok && issues)
        this.setState({ issues })
    })
  }

  nextIssuePage() {
    this.state.issuePage++
    loadIssueTransactions(this.state.product, this.state.issuePage, 6, (ok, issues) => {
      if (ok && issues)
        this.setState({ issues })
    })
  }

  renderIssueTransactions() {
    let issues = this.state.issues;
    if (!issues)
      return null;
    let content = issues.map(((res) => {
      let d = new Date();
      d.setTime(res.time)
      let btime = d.toLocaleString()
      //
      let info = '';
      if (res.info)
        info = new Buffer(res.info, 'base64').toString()
      let color = 'black'
      if (res.luckey) {
        color = 'red';
      }
      return (<tr>
        <td><AccountDialog rsId={res.from} /></td>
        <td style={{ color: color }}>{res.wp}%</td>
        <td style={{ color: color }}>{res.sp}%</td>
        <td>{btime}</td>
        <td>{info}</td>
      </tr>)
    }).bind(this));

    return content;
  }

  renderProductDetail() {
    if (!this.state.productDetail)
      return null;
    let count = this.state.productDetail.joinsCount;

    return (
      <div><Nav tabs>
        <NavItem>
          <NavLink
            className={classnames({ active: this.state.detailTab === '0' })}
            onClick={() => { this.onDetailTabClicked('0'); }}
          >
            <i className="icon-calculator"></i><Badge
              color="success">商品信息) </Badge>
          </NavLink>
        </NavItem>
        <NavItem>
          <NavLink
            className={classnames({ active: this.state.detailTab === '1' })}
            onClick={() => { this.onDetailTabClicked('1'); }}
          >
            <i className="icon-basket-loaded"></i><Badge pill color="danger">抢购记录-{count}</Badge>
          </NavLink>
        </NavItem>
        {this.renderIssueVoteTab()}
      </Nav>
        {
          this.renderProductTabs()
        }
      </div>)
  }

  renderIssueVoteTab() {
    let count = this.state.productDetail.issues.count;
    if (this.state.product.issue) {
      return (<NavItem>
        <NavLink
          className={classnames({ active: this.state.detailTab === '2' })}
          onClick={() => { this.onDetailTabClicked('2'); }}
        >
          <i className="icon-basket-loaded"></i><Badge pill color="danger">投票列表-{count}</Badge>
        </NavLink>
      </NavItem>)
    }
    return null;
  }


  renderPay10pbutton() {
    if (!this.state.productDetail)
      return null;
    let winner = this.state.productDetail.winner;
    if (!winner)
      return null;
    return (<ListGroupItem>
      <PopHint id="wind123" text={'抢购成功账户: (' + winner.amount + '积分)'} content='系统在所有抢购用户中随机计算一位成功者，计算方法与最后一位抢购用户的交易数据有关，在停止购买前，每次有新的用户加入都会重新计算成功者账户。所有数据与算法都是公开，可以重复验证的。' />
      <div><AccountDialog rsId={winner.accountId} /></div>
      <ActionButton product={this.state.product} productDetail={this.state.productDetail} winner={this.state.productDetail.winner} />
    </ListGroupItem>)
  }

  renderSteps() {
    if (true)
      return null;
    return (<Breadcrumb>
      <BreadcrumbItem><a href="#">Home</a></BreadcrumbItem>
      <BreadcrumbItem><a href="#">Library</a></BreadcrumbItem>
      <BreadcrumbItem active>Data</BreadcrumbItem>
    </Breadcrumb>)
  }

  renderIssueVoteList() {
    if (!this.state.productDetail || !this.state.productDetail.issues) {
      return null;
    }
    let count = this.state.productDetail.issues.count;
    let sp = this.state.productDetail.issues.result.sp;
    let wp = this.state.productDetail.issues.result.wp;
    let voter = this.state.productDetail.issues.result.voter;
    if (this.state.product.issue) {
      return (<div>
        <ListGroupItem><PopHint id='vw123' text='投票获奖励账户:' content='任何账户都可以投票分配总金额（商品价格+卖家保证金）的90%给买卖双方，帮助解决纠纷，投票结束系统将随机选取一批投票结果平均值作为最终买卖双方退款金额。同时自动把总金额10%奖励给与投票结果最接近的账户。' /> <AccountDialog rsId={voter} /> </ListGroupItem>
        <ListGroupItem>当前投票总计: {count}票</ListGroupItem>
        <ListGroupItem>投票结果买家应退款: {wp}%，卖家应退款: {sp}%</ListGroupItem>
      </div>);
    }
    return null;
  }

  renderProductDetailDialog() {
    let dueTime = ''
    let btime;
    if (this.state.product) {
      let d = new Date();
      if (this.state.productDetail) {
        d.setTime(this.state.productDetail.product.createTime)
        btime = d.toLocaleString()
      }
      if (this.state.product.status == 2) {
        if (this.state.productDetail && this.state.productDetail.product.due)
          dueTime = ' 还有' + this.state.productDetail.product.due + '分钟关闭抢购。'
      }
    }
    //
    return (<Modal isOpen={this.state.detail} toggle={this.toggleDetail.bind(this)}
      className={'modal-info ' + this.props.className}>
      <ModalHeader toggle={this.toggleDetail.bind(this)}>商品详细信息</ModalHeader>
      <ModalBody>
        <ListGroup>
          <ListGroupItem>商品：{this.state.product.title}</ListGroupItem>
          <ListGroupItem>卖家：<AccountDialog rsId={this.state.product.senderId} /></ListGroupItem>
          <ListGroupItem>上架时间：{btime}</ListGroupItem>
          <ListGroupItem>标签：{this.state.product.tags}</ListGroupItem>
          <ListGroupItem>卖家原价:{this.state.product.price} 积分 (抢购价:{(this.state.product.price / 10)} 积分-全国包邮-8天无理由退货) </ListGroupItem>
          <ListGroupItem>抢购总金额:<PopHint id={'tamt'} text={this.state.product.amout + '积分'} content='抢购总金额大于商品价格50倍或单个买家抢购金额达到商品价格的16小时后停止抢购。系统随机选取一名成功者，并自动退款给抢购未成功者。成功者发送收货地址等信息后卖家发货。系统补偿卖家差价。' /> {dueTime}</ListGroupItem>
          <ListGroupItem>卖家保证金:<PopHint id={'tamgr'} text={this.state.product.guarantee + '积分'} content='卖家在商品发布时候支付给平台的保证金,保证金保障商品真实与交易真实性.交易结束保证金自动退回到卖家账户.虚假交易全部金额补偿给买家.' /></ListGroupItem>
          <ListGroupItem><PopoverItem mode='step' product={this.state.product} /></ListGroupItem>
          {this.renderPay10pbutton()}
          {this.renderIssueVoteList()}
          <ListGroupItem>{this.renderProductDetail()}</ListGroupItem>
        </ListGroup>
      </ModalBody>
      <ModalFooter>
        <Button color="danger" onClick={this.toggleDetail.bind(this)}>取消</Button>
        {this.renderJoinButton1()}
      </ModalFooter>
    </Modal>)
  }
  renderJoinButton1() {
    if (!this.state.product)
      return null;
    if (this.state.product.status > 2)
      return null;
    let txt = (this.state.product.price / 10) + '积分抢购'
    return (<Button color="danger" onClick={e => {
      this.toggleDetail()
      this.joinProduct(this.state.product)
    }}>{txt}</Button>)
  }


  rederJoinProductDialog() {
    if (!this.state.product)
      return null;

    let priceHolder = '给喜欢的商品一个喜欢的价格(' + (this.state.product.price / 10) + '积分起)'
    let joinContent = this.state.product.price + '积分立即抢购成功。或者以原价一折的价格起任意加价抢购。系统机器人在所有抢购者中随机选择抢购成功账户（所有账户累计抢购金额大于商品卖家价格50倍），抢购金额越高,被机器人选中概率越大。抢购未成功全部自动退款。';
    return (<Modal isOpen={this.state.join} toggle={this.toggleJoin.bind(this)}
      className={'modal-info ' + this.props.className}>
      <ModalHeader toggle={this.toggleJoin.bind(this)}>参加抢购</ModalHeader>
      <ModalBody>
        <p>商品: {this.state.product.title}</p>
        <p>卖家原价:{this.state.product.price} 积分-全国包邮-8天无理由退货</p>
        <p>抢购价:{(this.state.product.price / 10)} 积分起</p>
        <FormGroup>
          <Label htmlFor="appendedInputButtons">账户密码:</Label>
          <div className="controls">
            <InputGroup>
              <BtbSecret onSecret={this.onSecret.bind(this)} />
            </InputGroup>
          </div>
        </FormGroup>
        <FormGroup>
          <div className="controls">
            <InputGroup className="input-prepend">
              <InputGroupAddon addonType="prepend">
                <InputGroupText><PopHint id="min123" text='参加抢购金额:' content={joinContent} /></InputGroupText>
              </InputGroupAddon>
              <Input id="prependedInput2" size="16" type="text" onChange={this.updateJoinAmount.bind(this)} placeholder={priceHolder} />
              <InputGroupAddon addonType="append">
                <InputGroupText>积分</InputGroupText>
              </InputGroupAddon>
            </InputGroup>
          </div>
        </FormGroup>
        <FormGroup>
          <div className="controls">
            <InputGroup className="input-prepend">
              <InputGroupAddon addonType="prepend">
                <InputGroupText><PopHint id="min123" text='机器人费:' content='机器人为账户余额比较多的账户，负责验证所有交易是否符合规则,并将合法交易保存到区块链。机器人保证整个系统按照规则运行。同时获得机器人费。' /></InputGroupText>
              </InputGroupAddon>
              <Input id="appendedPrependedInput" size="16" type="text" onChange={this.updateFee.bind(this)} placeholder='机器人费，最少0.2' />
              <InputGroupAddon addonType="append">
                <InputGroupText>积分</InputGroupText>
              </InputGroupAddon>
            </InputGroup>
          </div>
        </FormGroup>
        {
          this.renderDeadline()
        }
        <FormGroup>
          <Alert color="danger"><small>抢购成功者账户根据最后一位抢购者信息计算，下单成功后即可看到抢购结果。</small></Alert>
          <Alert color="danger"><small>所有商品支持8天无理由退货，退货情况系统自动给与卖方交易补偿。</small></Alert>
          <Alert color="danger"><small>抢购总金额超过商品价格50倍或单个买家金额达到商品价格后，系统机器人随机选取一位成功者作为买家。未成功者自动退回全部抢购金额。</small></Alert>
        </FormGroup>
        <FormGroup>
          {this.renderdialogMsg()}
        </FormGroup>
      </ModalBody>
      <ModalFooter>
        <Button color="danger" onClick={this.toggleJoin.bind(this)}>取消</Button>
        {this.renderActionButton()}
      </ModalFooter>
    </Modal>)
  }

  renderActionButton() {
    if (this.state.success)
      return null;
    return (<Button color="danger" onClick={this.doJoin.bind(this)}>抢购</Button>);
  }

  renderJoinButton(res) {
    let debug = false
    if (debug) {
      return (<Button size="sm" className="btn-facebook btn-brand mr-1 mb-1" onClick={(e) => this.joinProduct(res)}><i className="fa fa-facebook"></i><span>一折抢购</span></Button>);
    } else {
      let sts = getProductStatus(res.status)
      if (res.status > 2 || res.closed) {
        let nxt = getProductNextStep(res.status)
        return (<Button color="secondary" onClick={(e) => this.showDetailDialog(res)}><span>{nxt}</span></Button>);
      } else {
        let txt = (res.price / 10) + '积分抢购';
        return (<Button color="danger" onClick={(e) => this.joinProduct(res)}><span>{txt}</span></Button>);
      }
    }
  }

  renderProductList() {
    let list = this.state.list;
    if (!list)
      return null;
    let content = list.map((res) => {
      let d = new Date();
      d.setTime(res.createTime)
      let btime = d.toLocaleString()
      let url = getFileHost() + '/thumbnail/0/' + res.productId
      return (<tr>
        <td>{this.renderJoinButton(res)}</td>
        <td><img onClick={(e) => {
          e.preventDefault()
          this.showDetailDialog(res)
        }} style={{ width: 50, height: 50 }} src={url}></img></td>
        <td>
          <div><a href='#' onClick={(e) => {
            e.preventDefault()
            this.showDetailDialog(res)
          }}><span>{res.title}</span></a></div>
          <div><span onClick={(e) => {
            e.preventDefault()
            this.showDetailDialog(res)
          }} style={{ color: 'gray' }}>全国包邮-8天无理由退货</span></div>
        </td>
        <td>{btime}</td>
        <td><PopoverItem product={res} /></td>
      </tr >)
    });
    return content;
  }


  renderList() {
    let list = this.state.list;
    if (!list)
      return null;
    return (<Row>
      <Col>
        <Card>
          <CardBody>
            <InputGroup>
              <Input placeholder='搜索商品' onChange={this.upateFilterValue.bind(this)} />
              <Button color="primary" onClick={e => this.filterBlocks()}>
                <i className="fa fa-search"></i>&nbsp;
                </Button>
            </InputGroup>
            <Table hover bordered striped responsive size="sm">
              <tbody>
                {this.renderProductList()}
              </tbody>
            </Table>
            {this.rederAddProductDialog()}
            {this.rederJoinProductDialog()}
            {this.renderProductDetailDialog()}
            <nav>
              <Pagination>
                <PaginationItem><PaginationLink previous tag="button" onClick={(e) => { this.prePage() }}>上一页</PaginationLink></PaginationItem>
                <PaginationItem active>
                  <PaginationLink tag="button">{this.state.page}</PaginationLink>
                </PaginationItem>
                <PaginationItem><PaginationLink next tag="button" onClick={(e) => { this.nextPage() }}>下一页</PaginationLink></PaginationItem>
              </Pagination>
            </nav>
            <Button onClick={(e) => { this.togglePublishProduct() }} className="btn-openid btn-brand"><i className="fa fa-openid"></i><span>出售我的物品</span></Button>
          </CardBody>
        </Card>
      </Col>
    </Row>)
  }

  onTabClicked(tab) {
    if (this.state.activeTab !== tab) {
      this.setState({
        activeTab: tab,
      });
      let aid = 0;
      if (this.state.account) {
        aid = this.state.account.aid
      }
      loadProductList(this.onProductList.bind(this), 1, tab, aid + '')
    }
  }

  render() {
    return (
      <div className="animated fadeIn">
        <Nav tabs>
          <NavItem>
            <NavLink
              className={classnames({ active: this.state.activeTab === '0' })}
              onClick={() => { this.onTabClicked('0'); }}
            >
              <i className="icon-calculator"></i> {'\u00A0'}<Badge
                color="info">一折抢购-8天无理由退货</Badge>
            </NavLink>
          </NavItem>
          <NavItem>
            <NavLink
              className={classnames({ active: this.state.activeTab === '1' })}
              onClick={() => { this.onTabClicked('1'); }}
            >
              <i className="icon-basket-loaded"></i> <span
                className={this.state.activeTab === '1' ? '' : 'd-none'}>拼手气</span>{'\u00A0'}<Badge pill color="info">我的抢购</Badge>
            </NavLink>
          </NavItem>
          <NavItem>
            <NavLink
              className={classnames({ active: this.state.activeTab === '2' })}
              onClick={() => { this.onTabClicked('2'); }}
            >
              <i className="icon-pie-chart"></i> <span className={this.state.activeTab === '2' ? '' : 'd-none'}>我是卖家</span><Badge pill color="info">我的物品</Badge>
            </NavLink>
          </NavItem>
          <NavItem>
            <NavLink
              className={classnames({ active: this.state.activeTab === '3' })}
              onClick={() => { this.onTabClicked('3'); }}
            >
              <i className="icon-pie-chart"></i> <span className={this.state.activeTab === '3' ? '' : 'd-none'}>我要投票</span><Badge pill color="info">纠纷交易</Badge>
            </NavLink>
          </NavItem>
        </Nav>
        {this.renderList()}
      </div>

    );
  }
}


class PopoverItem extends Component {
  constructor(props) {
    super(props);

    this.toggle = this.toggle.bind(this);
    this.state = {
      popoverOpen: false
    };
  }

  toggle() {
    this.setState({
      popoverOpen: !this.state.popoverOpen,
    });
  }

  renderSteps() {
    let id = 'steps-' + this.props.product.transactionId;
    let stepInfo = ''
    if (this.props.product.steps) {
      let ss = this.props.product.steps.split(';');
      let content = ss.map((step, idx) => {
        let res;
        if (step && step.length > 0) {
          res = getProductStatus(step);
        }
        if (!res)
          return null;
        return (<ListGroupItem>{(idx + 1) + "." + res}</ListGroupItem>);
      })
      stepInfo = content;
    }

    let nxt = getProductNextStep(this.props.product.status);
    if (nxt) {
      nxt = '下一步-> ' + nxt;
    }
    let nxtUi = (<Alert onClick={this.toggle}><small> {nxt} </small> </Alert>);
    if (this.props.product.status == 99) {
      nxtUi = null;
    }
    return (
      <span>
        <Alert id={id} onClick={this.toggle}><small>当前状态: {getProductStatus(this.props.product.status)}  </small> </Alert>
        {nxtUi}
        <Popover placement='top' isOpen={this.state.popoverOpen} target={id} toggle={this.toggle}>
          <PopoverHeader>状态列表</PopoverHeader>
          <PopoverBody>{stepInfo}</PopoverBody>
        </Popover>
      </span>
    )
  }

  render() {
    if ('step' === this.props.mode)
      return (<ListGroup size='sm'>
        {this.renderSteps()}
      </ListGroup>);
    let id = 'Popover-' + this.props.product.transactionId;
    let link = getCurrentHost() + "/#/bshop/product-list#" + this.props.product.transactionId + '#【眞客】' + this.props.product.title + ' ' + (this.props.product.price / 10) + '积分抢购.8天无理由退货';

    return (
      <span>
        <span className='alert-primary  fade show' id={id} onClick={this.toggle}>
          邀请好友抢购
        </span>
        <Popover placement='top' isOpen={this.state.popoverOpen} target={id} toggle={this.toggle}>
          <PopoverHeader>分享这个商品的链接</PopoverHeader>
          <PopoverBody>{link}</PopoverBody>
        </Popover>
      </span>
    );
  }
}

export default ProductList;
