import React, { Component } from 'react';
import { Modal, ModalBody, ModalFooter, ModalHeader, Button } from 'reactstrap'
import Cropper from 'react-easy-crop'
import getCroppedImg from './cropImg'
function readFile(file) {
    return new Promise(resolve => {
        const reader = new FileReader()
        reader.addEventListener('load', () => resolve(reader.result), false)
        reader.readAsDataURL(file)
    })
}

export default class ImgDialog extends Component {
    constructor(props) {
        super(props)
        this.state = {
            mode: 'simple', editing: false, imageSrc: null,
            crop: { x: 0, y: 0 },
            zoom: 1,
        }
    }

    toggleEditor() {
        this.setState({ editing: !this.state.editing, imageSrc: null })
    }

    onCropChange = crop => {
        this.setState({ crop })
    }

    onCropComplete = (croppedArea, croppedAreaPixels) => {
        console.log(croppedArea, croppedAreaPixels)
        this.setState({ croppedAreaPixels })
    }

    onZoomChange = zoom => {
        this.setState({ zoom })
    }

    onFileChange = async e => {
        if (e.target.files && e.target.files.length > 0) {
            const imageDataUrl = await readFile(e.target.files[0])
            this.setState({
                imageSrc: imageDataUrl,
                crop: { x: 0, y: 0 },
                zoom: 1,
            })
        }
    }

    showCroppedImage = async () => {
        const croppedImage = await getCroppedImg(
            this.state.imageSrc,
            this.state.croppedAreaPixels
        )
        this.setState({ croppedImage })
        this.props.onFinish(this.state.croppedImage)
        this.toggleEditor()
    }

    renderEditor() {
        return (<Modal isOpen={this.state.editing} toggle={this.toggleEditor.bind(this)}
            className={'modal-info ' + this.props.className}>
            <ModalHeader toggle={this.toggleEditor.bind(this)}>{this.props.title}</ModalHeader>
            <ModalBody>
                <div style={{ width: 200, height: 300 }}>
                    <input type="file" onChange={this.onFileChange} />
                    {this.state.imageSrc && (
                        <Cropper
                            image={this.state.imageSrc}
                            crop={this.state.crop}
                            zoom={this.state.zoom}
                            aspect={this.state.aspect}
                            onCropChange={this.onCropChange}
                            onCropComplete={this.onCropComplete}
                            onZoomChange={this.onZoomChange}
                        />)}
                </div>
            </ModalBody>
            <ModalFooter>
                <Button color="danger" onClick={this.showCroppedImage.bind(this)}>完成</Button>
            </ModalFooter>
        </Modal>)
    }

    render() {
        return (<div>
            {this.renderEditor()}
            <a href="#" onClick={(e) => { e.preventDefault(); this.toggleEditor() }}>{this.props.title}</a>
            {this.state.croppedImage && (<img style={{ width: 50, height: 50 }} src={this.state.croppedImage} ></img>)}
        </div>)
    }
}